<template>
	<div class="authority_wrap" data-commont="新建素材">
		<el-row style="background: #fff;padding: 15px 0;border-bottom: 1px solid #eee;">
			<el-col :span="2" :offset = "21">
				<el-button icon = "el-icon-plus" type = "primary" @click="addRoleTree">添加角色</el-button>
			</el-col>
		</el-row>
		<!--创建角色弹出框-->
	    <el-dialog
		    title="请创建角色"
		    :visible.sync="createDialogVisible"
		    width="40%"
		    :before-close="handleClose">
		 	<el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
		 		<el-form-item label="角色名称:" prop="name">
			    	<el-input v-model="ruleForm.name" placeholder="角色名称" style="width:90%;"></el-input>
			    </el-form-item>
			    <el-form-item label="功能权限:">
			    	<el-tree
						:data="cascaderData"
						show-checkbox
						node-key="id"
						style="height: 150px;overflow:auto;"
						:props="defaultProps">
					</el-tree>
				</el-form-item>
			</el-form>
		    <span slot="footer" class="dialog-footer">
			    <el-button type="primary" @click="addTreeFun('ruleForm')">保存</el-button>
		  	</span>
		</el-dialog>
   <!--table-->
		<el-table :data="roleData" style="width: 100%":show-header=false>
		    <el-table-column type="expand">
		      	<template slot-scope="scope">
		        	<div  style="height: 150px; overflow: auto;">
		        		<el-table :data="peopleData">
					        <el-table-column prop="orgName" label="部门名称">
					        </el-table-column>
					        <el-table-column  prop="orgCode"label="部门编码" >
					        </el-table-column>
					        <el-table-column prop="loginName" label="登录账号">	
					        </el-table-column>
					        <el-table-column label="密码重置" width="260" >
					      		<template slot-scope="scope">
					      			<el-button>密码重置</el-button>
					      		</template>
					        </el-table-column>
					    </el-table>
		        	</div>
		
	      		</template>
		    </el-table-column>
			
		    <el-table-column>
		    	<template slot-scope="scope">
		    		<span>{{scope.row.name}}</span>
		    	</template>
		    </el-table-column>	
		     <el-table-column>
		    	<template slot-scope="scope">
		    		<span style="float: right;" >
		    			<el-button @click="boundMemberDiadig = true">绑定成员</el-button>
		    		</span>
		    	</template>
		    </el-table-column>		   
		</el-table>
		
		<!--绑定成员弹出框-->
		<el-dialog
		    title="选择绑定成员"
		    :visible.sync="boundMemberDiadig"
		    width="60%"
		    :before-close="handleClose">
			<el-row :gutter="20">
			 	<el-col :span="8">
			 		<h3>选择:</h3>
			 		<el-input placeholder="搜索部门"  v-model="filterText">
					</el-input>
					<el-tree
					  class="filter-tree"
					  :data="cascaderData"
					  :filter-node-method="filterNode"
					  ref="tree">
					</el-tree>
			 	</el-col>
			 	<el-col :span="14">
			 		<h3>已选</h3>
			 		<el-table
					    ref="multipleTable"
					    :data="peopleData"
					    style="width: 100%">
					    <el-table-column type="selection" width="55">
					    </el-table-column>
					    <el-table-column  label="岗位">
					        <template slot-scope="scope">{{ scope.row.post}}</template>
					    </el-table-column>
					    <el-table-column  prop="name" label="姓名">
					    </el-table-column>
					</el-table>
			 	</el-col>
			</el-row>
		  <span slot="footer" class="dialog-footer">
		    <el-button type="primary" @click="boundMemberDiadig = false">确 定</el-button>
		  </span>
		</el-dialog>
	</div>
</template>

<script>
	import cascader from './cascader'
	import tableData from './table'
	import roleData from './roleData' //角色
	import peopleData from './peopleData' //绑定人
	import {
	  	getUserInfo,
	  	getUsers
	} from 'utils/auth';
	import {
		leftMenuPort
	} from 'api/model/leftMenu';   //权限树
		export default {
	    name: 'authority',
	    
	    data() {
	      	return {
		        createDialogVisible: false,
				cascaderData:cascader,
				tableData:tableData,
				roleData:roleData,
				peopleData:peopleData,
				boundMemberDiadig:false,  //绑定成员弹出框
				filterText:'', //绑定选择input值
				multipleSelection: [], //多选表格
				//tree字段命名
				defaultProps: {
		          children: 'subMenu',
		          label: 'MENU_NAME'
		        },
				//创建者弹出框
				ruleForm:{
					name:''
				},
				rules: {
				 	name: [
			            { required: true, message: '请输入角色名称', trigger: 'blur' },
			            { min: 3, max: 15, message: '长度在 3 到 15 个字符', trigger: 'blur' }
			        ],
			       
				}
	      	}
	    },
	    watch: {
	        filterText(val) {
	        	this.$refs.tree.filter(val);
	        }
	    },
	    computed: {
	    	
	    },
	    methods:{
	    	//添加角色确定
	    	addTreeFun(formName){

	    		this.$refs[formName].validate((valid) => {
					if (valid) {
	    				this.createDialogVisible = false
	    				this.roleData.push({
	    					name:this.ruleForm.name
	    				})
	    				
					} else {
						console.log('error submit!!');
						return false;
					}
				});
	    	
	    	},
	    	//添加权限树
	    	addRoleTree(){
	    		this.createDialogVisible = true
	    		let req = {
		    		USER_ID:getUsers(),  // 
		    		RIGHTS:getUserInfo().RIGHTS,
		    		PLATFORM:'1'    //系统平台
	    	    }
   			
		    	leftMenuPort(req).then( data => {
		    	 	this.cascaderData = data
	//		  		console.log('菜单::'+JSON.stringify(data))
			  		
			    })
	    	},
	    	//绑定选择人员tree筛选
	    	filterNode(value, data) {
        		if (!value) return true;
        		return data.label.indexOf(value) !== -1;
      		},
	    	handleClose(done) {
		        this.$confirm('确认关闭？')
		          .then(() => {
		            done();
		          })
		          .catch(() => {});
		      	}
			}
  	}

</script>

<style lang="scss" slot-scope="scope">
	$border:1px solid #eee;
	.authority_wrap{
		
	
	}
	
</style>


